import { useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { getAuth, signOut } from 'firebase/auth';
import {
  FiGrid, FiUsers, FiPlus, FiClipboard, FiFileText,
  FiBarChart2, FiUser, FiLogOut, FiMenu, FiX, FiChevronDown,
  FiChevronRight, FiSettings, FiDollarSign, FiCalendar,
  FiTrendingUp, FiCheckCircle, FiMail, FiShield, FiUserPlus
} from 'react-icons/fi';

export default function Sidebar({ collapsed, onToggle, mobileMenuOpen, onMobileClose }) {
  const location = useLocation();
  const navigate = useNavigate();
  const [expandedSections, setExpandedSections] = useState({
    vendors: false,
    operations: false,
    management: false,
    reports: false
  });

  const handleLogout = async () => {
    try {
      const auth = getAuth();
      await signOut(auth);
      navigate('/admin/login');
    } catch (error) {
      console.error('Error signing out:', error);
    }
  };

  const toggleSection = (section) => {
    setExpandedSections(prev => ({
      ...prev,
      [section]: !prev[section]
    }));
  };

  const isActive = (path) => {
    return location.pathname === path;
  };

  const isParentActive = (paths) => {
    return paths.some(path => location.pathname.startsWith(path));
  };

  return (
    <div className={`fixed inset-y-0 left-0 transform ${mobileMenuOpen ? 'translate-x-0' : '-translate-x-full'} 
      md:relative md:translate-x-0 transition-transform duration-200 ease-in-out z-30 
      ${collapsed ? 'w-16' : 'w-64'} bg-gray-900 text-white shadow-lg`}>
      
      {/* Simple Header */}
      <div className="p-4 border-b border-gray-700">
        <div className="flex items-center justify-between">
          {!collapsed ? (
            <h2 className="text-xl font-bold text-white">VMT Admin</h2>
          ) : (
            <h2 className="text-xl font-bold text-white mx-auto">VM</h2>
          )}
          
          <button 
            onClick={onToggle}
            className="p-1 rounded hover:bg-gray-700 hidden md:block"
          >
            {collapsed ? <FiMenu size={20} /> : <FiX size={20} />}
          </button>
          
          <button 
            onClick={onMobileClose}
            className="p-1 rounded hover:bg-gray-700 md:hidden"
          >
            <FiX size={20} />
          </button>
        </div>
      </div>
      
      {/* Navigation */}
      <nav className="flex-1 overflow-y-auto pb-24">
        <div className="px-2 py-2">
          <SidebarItem 
            to="/admin/dashboard" 
            icon={<FiGrid size={20} />}
            text="Dashboard" 
            active={isActive('/admin/dashboard')}
            collapsed={collapsed}
          />
        </div>
        
        {/* Vendor Management Section */}
        <SidebarSection
          title="Vendor Management"
          icon={<FiUsers size={16} />}
          collapsed={collapsed}
          expanded={expandedSections.vendors}
          onToggle={() => toggleSection('vendors')}
          active={isParentActive(['/admin/vendors', '/admin/add-vendor', '/admin/vendor-performance'])}
        >
          <SidebarItem 
            to="/admin/vendors" 
            icon={<FiUsers size={18} />}
            text="All Vendors" 
            active={isActive('/admin/vendors')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/add-vendor" 
            icon={<FiPlus size={18} />}
            text="Add Vendor" 
            active={isActive('/admin/add-vendor')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/vendor-performance" 
            icon={<FiTrendingUp size={18} />}
            text="Performance" 
            active={isActive('/admin/vendor-performance')}
            collapsed={collapsed}
            isChild
          />
        </SidebarSection>
        
        {/* Operations Management Section */}
        <SidebarSection
          title="Operations Management"
          icon={<FiClipboard size={16} />}
          collapsed={collapsed}
          expanded={expandedSections.operations}
          onToggle={() => toggleSection('operations')}
          active={isParentActive(['/admin/purchase-orders', '/admin/create-po', '/admin/delivery-challans', '/admin/create-challan', '/admin/items'])}
        >
          <SidebarItem 
            to="/admin/purchase-orders" 
            icon={<FiFileText size={18} />}
            text="All POs" 
            active={isActive('/admin/purchase-orders')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/create-po" 
            icon={<FiDollarSign size={18} />}
            text="Create PO" 
            active={isActive('/admin/create-po')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/delivery-challans" 
            icon={<FiClipboard size={18} />}
            text="Delivery Challans" 
            active={isActive('/admin/delivery-challans')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/create-challan" 
            icon={<FiPlus size={18} />}
            text="Create Challan" 
            active={isActive('/admin/create-challan')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/items" 
            icon={<FiClipboard size={18} />}
            text="Items" 
            active={isActive('/admin/items')}
            collapsed={collapsed}
            isChild
          />
        </SidebarSection>
        
        {/* Management Tools Section */}
        <SidebarSection
          title="Management Tools"
          icon={<FiSettings size={16} />}
          collapsed={collapsed}
          expanded={expandedSections.management}
          onToggle={() => toggleSection('management')}
          active={isParentActive(['/admin/status-manager', '/admin/daily-todos', '/admin/reports'])}
        >
          <SidebarItem 
            to="/admin/status-manager" 
            icon={<FiCheckCircle size={18} />}
            text="Status Manager" 
            active={isActive('/admin/status-manager')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/daily-todos" 
            icon={<FiCalendar size={18} />}
            text="Daily Todos" 
            active={isActive('/admin/daily-todos')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/reports" 
            icon={<FiBarChart2 size={18} />}
            text="Reports" 
            active={isActive('/admin/reports')}
            collapsed={collapsed}
            isChild
          />
        </SidebarSection>
        
        {/* Reports & Analytics Section */}
        <SidebarSection
          title="Reports & Analytics"
          icon={<FiBarChart2 size={16} />}
          collapsed={collapsed}
          expanded={expandedSections.reports}
          onToggle={() => toggleSection('reports')}
          active={isParentActive(['/admin/analytics', '/admin/customers'])}
        >
          <SidebarItem 
            to="/admin/analytics" 
            icon={<FiTrendingUp size={18} />}
            text="Analytics" 
            active={isActive('/admin/analytics')}
            collapsed={collapsed}
            isChild
          />
          <SidebarItem 
            to="/admin/customers" 
            icon={<FiUser size={18} />}
            text="Customers" 
            active={isActive('/admin/customers')}
            collapsed={collapsed}
            isChild
          />
        </SidebarSection>
        
        {/* Quick Actions (when collapsed) */}
        {collapsed && (
          <>
            <div className="border-t border-indigo-600 mt-4 pt-4"></div>
            <SidebarItem 
              to="/admin/add-vendor" 
              icon={<FiPlus size={20} />}
              text="Add Vendor" 
              active={isActive('/admin/add-vendor')}
              collapsed={collapsed}
              tooltip="Add Vendor"
            />
            <SidebarItem 
              to="/admin/assign-job" 
              icon={<FiClipboard size={20} />}
              text="Assign Job" 
              active={isActive('/admin/assign-job')}
              collapsed={collapsed}
              tooltip="Assign Job"
            />
            <SidebarItem 
              to="/admin/create-po" 
              icon={<FiFileText size={20} />}
              text="Create PO" 
              active={isActive('/admin/create-po')}
              collapsed={collapsed}
              tooltip="Create PO"
            />
          </>
        )}
      </nav>
      
      {/* Logout Button */}
      <div className="absolute bottom-0 left-0 right-0 p-3 border-t border-gray-700 bg-gray-800">
        <button
          onClick={handleLogout}
          className={`flex items-center p-2 rounded transition-colors w-full hover:bg-gray-700`}
          title={collapsed ? "Logout" : undefined}
        >
          <span className={`${collapsed ? 'mx-auto' : 'mr-3'}`}>
            <FiLogOut size={20} />
          </span>
          {!collapsed && <span className="font-medium">Logout</span>}
        </button>
      </div>
    </div>
  );
}

// Simple Sidebar Section Component
function SidebarSection({ title, icon, children, collapsed, expanded, onToggle, active }) {
  if (collapsed) {
    return null;
  }

  return (
    <div className="mt-2">
      <button
        onClick={onToggle}
        className={`flex items-center justify-between w-full px-3 py-2 text-sm font-medium transition-colors
          ${active ? 'text-white bg-gray-700' : 'text-gray-300 hover:text-white hover:bg-gray-800'}`}
      >
        <div className="flex items-center">
          {icon}
          <span className="ml-2">{title}</span>
        </div>
        {expanded ? <FiChevronDown size={14} /> : <FiChevronRight size={14} />}
      </button>
      
      {expanded && (
        <div className="mt-1 pb-2">
          {children}
        </div>
      )}
    </div>
  );
}

// Simple Sidebar Item Component
function SidebarItem({ to, icon, text, active, collapsed, isChild = false, tooltip }) {
  const baseClasses = `flex items-center transition-colors relative group
    ${isChild ? 'ml-6 py-2 px-3' : 'p-3 mx-2 my-1 rounded'}
    ${active 
      ? 'bg-indigo-600 text-white' 
      : 'text-gray-300 hover:bg-gray-700 hover:text-white'
    }`;

  return (
    <Link
      to={to}
      className={baseClasses}
      title={collapsed ? tooltip || text : undefined}
    >
      <span className={`${collapsed ? 'mx-auto' : 'mr-3'} flex-shrink-0`}>
        {icon}
      </span>
      {!collapsed && (
        <span className={`${isChild ? 'text-sm' : 'font-medium'} truncate`}>
          {text}
        </span>
      )}
      
      {/* Simple tooltip for collapsed state */}
      {collapsed && (tooltip || text) && (
        <div className="absolute left-full ml-2 px-2 py-1 bg-gray-900 text-white text-sm rounded opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap z-50">
          {tooltip || text}
        </div>
      )}
    </Link>
  );
}
